@import url('https://fonts.googleapis.com/css2?family=Alkatra:wght@400;500;600&family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');
* {
	margin: 0;
	padding: 0;
	border: none;
	font-family: 'Open Sans', sans-serif;
	outline: none;
}
html, body, #root {
	height: 100%;
	color: white;
	background-color: rgb(12, 7, 0);
}
ul {
	list-style: none;
}
a {
	text-decoration: none;
	color: white
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}
::-webkit-scrollbar {
	width: 10px;
	border-radius: 10px;
	background-color: #161616;
  }
::-webkit-scrollbar-thumb {
	background-color: #555;
	border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
	background-color: #666;
}